



import { Vue, Prop, Component } from "vue-property-decorator";

const DAYS = [
    "()",
    "()",
    "()",
    "()",
    "()",
    "()",
    "()"
];

/**
 * @summary .
 */
@Component
export default class DateTimeText extends Vue
{
    @Prop({ default: undefined }) text?: string;

    private get displayHtml(): string
    {
        const date = this.text ? new Date(this.text) : new Date();
        let dateStr = `${date.getFullYear()}<small></small>${date.getMonth() + 1}<small></small>${date.getDate()}<small></small>`;
        dateStr += `${DAYS[date.getDay()]} ${date.getHours()}<small></small>${date.getMinutes()}<small></small>`;
        return dateStr;
    }
}
